import React from 'react';

import {
  SlideContainer,
  TextContainer,
  ImageContainer,
  Caption,
} from '../components/slide';
import Slide from '../types/Slide';
import image from '../images/image5.png';
import Ref from '../components/Ref';

const caption = `Python's sqlite3 documentation warns against SQL injection`

const TakeawaysSlide: Slide = (props) => {
  return (
    <SlideContainer background={'dark'}>
      <TextContainer>
        <h1>Takeaways</h1>
        <p>
          The explored example is a simplification. Real applications have
          database schemas that are much more complicated, and real applications
          (hopefully) don't store passwords in plain text.
        </p>
        <p>
          However, the takeaway for developers is the same: user input should
          {' '}<strong>not</strong> be trusted. Sanitize all user input!
        </p>
        <p>
          Fortunately, many resources are available to responsible software
          engineers to make sanitation easy. SQL libraries in many different
          languages, such as{' '}
          <a href='https://docs.python.org/3/library/sqlite3.html' target='blank'>
            Python
          </a>
          , have user input sanitation built in.</p>
        <p>
          With just some simple integration of mindful coding practices and
          preventative methods such as penetration testing, web applications
          become that much more secure.
        </p>
        <p>
          (Bonus image: XKCD has a{' '}
          <a href='https://xkcd.com/327/' target='blank'>relevant comic</a>
          {' '}about SQL injection.)
        </p>
      </TextContainer>
      <ImageContainer>
        <img className={'side-image'} src={image} alt={caption} />
        <Caption>{caption} <Ref id={'1'} />.</Caption>
      </ImageContainer>
    </SlideContainer>
  );
};

export default TakeawaysSlide;
