import React from 'react';
import styled from 'styled-components';

import Slide from '../types/Slide';

const SlideContainer = styled.div`
  color: black;
  width: 100vw;
  background-color: #d0d0d0;
  overflow-x: hidden;
  justify-content: center;
  flex: 1;
  text-align: left;
`;

const TextContainer = styled.div`
  width: 40%;
  padding-left: 10%;
`;

const WhatHappenedSlide: Slide = (props) => {
  return (
    <SlideContainer>
        <TextContainer>
        <h1>What Happened?</h1>
        <p>The query gave the information of all the users, not just one!</p>
        <p>
          The software engineers for our example application did not sanitize
          user inputs. In other words, the application does not check if the
          user's input would alter the SQL query; user input is directly
          inserted into the query, which is interpreted by the database as
          entirely different instructions.
        </p>
        <p>
          In this case, <code>OR 1=1</code> is always true (1 always equals
          1), so all users in the database satisfy the query. Hence, all users
          show up in the final result, and the attacker has access to all users'
          private information.
        </p>
        <p>
          SQL injection attacks can be very costly and can put millions of
          people's privacy at risk. As of 2017, injection attacks remain the
          most common type of cyberattack.
        </p>
      </TextContainer>
    </SlideContainer>
  );
};

export default WhatHappenedSlide;
